.subscriptions {
    width: 82.5%;
    margin: 10vh auto 12.5vh;
    .heading {
        width: 35%;
        border-bottom: 1px solid rgb(170, 170, 170);
        h1 {
            padding-bottom: 1.5rem;
            font-size: 2.3rem;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            .icon {
                margin-right: 1rem;
            }
        }
        .options-wrapper {
            display: flex;
            .option {
                cursor: pointer;
                color: rgb(190, 190, 190);
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 1.05rem;
                font-weight: 600;
                padding: 0.5rem 0 11px;
                width: 40%;
                min-width: 160px;
                margin-bottom: -2px;
                &.active {
                    color: rgb(30, 30, 30);
                    border-bottom: 3px solid rgb(85, 85, 85);
                    padding-bottom: 8px;
                }
                .icon {
                    margin-right: 0.5rem;
                    &.cross {
                        font-size: 0.9rem;
                    }
                }
            }
        }
    }
    .subscriptions-container {
        margin: 0rem auto 1rem;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        .subscription-category-wrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            justify-content: space-between;
            width: fit-content;
            font-size: 1.05rem;
            font-weight: 600;
            margin: 2.25rem 0 1.25rem;
            &:first-of-type {
                margin: 1.75rem 0 1rem;
            }
            .arrow-icon {
                font-size: 0.9rem;
                cursor: pointer;
                transition: .1s;
                &.rotated {
                    rotate: -90deg;
                }
            }
            .category-name {
                display: flex;
                align-items: center;
                margin: 0 1.5rem;
                .icon {
                    margin-right: 0.35rem;
                    color: rgb(75, 75, 75);
                }
            }
            .price {
                font-weight: 500;
                background-color: lightgray;
                color: #000;
                padding: 2px 5px;
                border-radius: 4px;
            }
        }
        .susbscriptions-categories-header {
            display: flex;
            flex-direction: row;
            border-bottom: 1px solid rgb(160, 160, 160);
            &#hide {
                display: none;
            }
            .category-item {
                height: 1rem;
                width: 14.285%;
                width: 15%;
                font-weight: 500;
                font-size: 15px;
                color: rgb(170, 170, 170);
                margin-bottom: 0.325rem;
                text-align: center;
                &:last-of-type {
                    width: 10%;
                }
                &.item-icon {
                    width: 27.5px;
                    color: rgb(195, 195, 195);
                }
            }
        }
        .subscription-item-row {
            display: flex;
            flex-direction: row;
            border-bottom: 1px solid rgb(160, 160, 160);
            transition: .225s ease-in-out;
            transition: 0.4s ease-in-out;
            &:hover {
                transition: .2s ease-in-out;
                background-color: #e8e8e8;
            }
            &#hide {
                display: none;
            }
            .subsc-item {
                font-weight: 600;
                width: 14.285%;
                width: 15%;
                font-size: 16.25px;
                margin: 0.15rem 0;
                height: 3rem;
                display: flex;
                justify-content: center;
                align-items: center;
                .billing {
                    background-color: rgb(215, 215, 215);
                    padding: 3px 5px;
                    border-radius: 4px;
                    font-weight: 500;
                }
                &.item-remove {
                    width: 10%;
                    font-weight: 700;
                    font-size: 1.05rem;
                    cursor: pointer;
                    transition: .2s;
                    &:hover {
                        color: red;
                    }
                }
            }
            .subsc-item-icon {
                width: 27.5px;
                font-size: 1.25rem;
                display: flex;
                justify-content: center;
                align-items: center;
                .spotify {
                    color: #1DB954
                }
                .netflix {
                    color: #db0000;
                }
                .apple-music {
                    color: #fc3c44;
                }
                .youTube {
                    color: #c4302b;
                }
                .disney {
                    color: #000c7c;
                }
                .xbox {
                    color: #107C10;
                }
                .psPlus {
                    color: #4d83c1;
                }
                .strava {
                    color: #fc4c02;
                }
                .tinder {
                    color: #FF655B;
                }
                .iCloud {
                    color: #0a84ff;
                }
                .paramountPlus {
                    color: #11518a;
                }
                .nordVPN {
                    color: #4687FF
                }
            }
        }
    }
}

@media (max-width: 1450px) {
    .subscriptions {
        width: 87.5%;
    }
}

@media (max-width: 1200px) {
    .subscriptions {
        width: 92.5%;
        .heading {
            width: 40%;
            .options-wrapper {
                .option {
                    width: 45%;
                    min-width: 180px;
                }
            }
        }
        .subscriptions-container {
            .susbscriptions-categories-header {
                .category-item {
                    font-size: 14px;
                    font-weight: 400;
                }
            }
            .subscription-item-row {
                .subsc-item {
                    font-size: 15px;
                    font-weight: 500;
                }
            }
        }
    }
}

@media (max-width: 550px) {
    .subscriptions {
        width: 97.5%;
        .heading {
            margin: 0 auto;
            width: 92.5%;
            h1 {
                font-size: 2.5rem;
                margin-left: 0.25rem;
                justify-content: center;
            }
            .options-wrapper {
                justify-content: center;
                .option {
                    width: 50%;
                    font-size: 1rem;
                    color: rgb(200, 200, 200);
                    font-weight: 500;
                    &.active {
                        font-weight: 700;
                    }
                    .icon {
                        margin-right: 0.55rem;
                    }
                }
            }
        }
        .subscriptions-container {
            .subscription-category-wrapper {
                font-size: 0.9rem;
            }
            .susbscriptions-categories-header {
                .category-item {
                    font-size: 9.5px;
                    font-weight: 400;
                    align-items: flex-start;
                    width: 18.2%;
                    &.item-icon {
                        width: 25px;
                    }
                    &:last-of-type {
                        width: 9%;
                    }
                    &.subsc-date-mobile {
                        display: none;
                    }
                }
            }
            .subscription-item-row {
                .subsc-item-icon {
                    width: 25px;
                }
                .subsc-item {
                    font-size: 12px;
                    font-weight: 500;
                    width: 18.2%;
                    text-align: center;
                    &.item-remove {
                        width: 9%;
                        font-size: 15px;
                    }
                    &.subsc-date-mobile {
                        display: none;
                    }
                }
            }
        }
    }
}