.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    backdrop-filter: blur(2.5px);
    display: block;
    &.hide-modal-overlay {
        display: none;
    }
}

.subscription-form-modal {
    margin: 5vh auto;
    padding: 3rem 10%;
    width: auto;
    height: auto;
    text-align: center;
    border-radius: 8px;
    background-color: #f1f1f1;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
    opacity: 1;
    transition: .3s ease-in-out;
    &.hide-modal {
        transition: .225s ease-out;
        opacity: 0;
        z-index: -1000;
    }
    form {
        .close-modal {
            display: block;
            position: absolute;
            top: 1.1rem;
            right: 1.5rem;
            cursor: pointer;
            font-size: 2rem;
            color: #000;
            transition: .125s ease-in-out;
            &:hover {
                color: red;
            }
        }
        label {
            display: block;
            font-size: 1.65rem;
            font-weight: 500;
            margin: 1.75rem auto 0.25rem;
            letter-spacing: -0.2px;
            &:first-of-type {
                margin-top: 0.25rem;
            }
        }
        .subscription-select {
            color: #000;
            background-color: #c3c3c3;
            padding: 0.5rem 1rem;
            min-width: 200px;
            font-size: 1.175rem;
            border-radius: 4px;
            outline: none;
            border: 1px solid transparent;
            transition: .2s ease-in-out;
            &:hover {
                border: 1px solid #000;
            }
            option {
                background-color: #e2e2e2;
                color: #000;
            }
        }
        .subsType {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
            p {
                display: flex;
                justify-content: space-between;
                align-items: center;
                font-size: 1.225rem;
                margin: 0.2rem 0.35rem 0.75rem;
                padding: 0.6rem 1.4rem;
                background-color: #c3c3c3;
                color: #000;
                border: 1px solid #000;
                border-radius: 7px;
                cursor: pointer;
                transition: .2s ease-in-out;
                &:hover {
                    background-color: rgb(15, 15, 15);
                    color: #fff;
                }
                &.active {
                    background-color: transparent;
                    border: 2px solid #ba181b;
                    border-radius: 20px;
                    color: #ba181b;
                }
                .icon {
                    margin-right: 0.35rem;
                }
            }
        }
    }
    .buttons-wrapper {
        display: flex;
        button {
            width: 8vw;
            border: 1px solid #000;
            padding: 0.85rem 1.25rem;
            margin: 0.25rem 0.7rem 0.75rem;
            color: #000;
            background-color: transparent;
            font-weight: 700;
            font-size: 1.05rem;
            cursor: pointer;
            user-select: none;
            transition: .2s ease-in-out;
            &:nth-of-type(2) {
                background-color: #000;
                color: #fff;
                &:hover {
                    background-color: rgb(25, 25, 25);
                    background-color: darkblue;
                    transition: .2s ease-in-out;
                }
            }
            &:hover {
                color: #fff;
                background-color: #000;
                transition: .2s ease-in-out;
            }
        }
    }
}

.input-container {
    position: relative;
    margin: 1.75rem auto;
    width: 200px;
}

.input-container input[type="number"] {
    font-size: 20px;
    width: 100%;
    border: none;
    border-bottom: 2px solid #bebebe;
    padding: 5px 0;
    background-color: transparent;
    outline: none;
    text-align: center;
}

.input-container .label {
    font-size: 1.25rem;
    position: absolute;
    top: 0;
    left: 0;
    color: #333;
    transition: all 0.3s ease;
    pointer-events: none;
    letter-spacing: 0;
    user-select: none;
    font-weight: 400;
}

.input-container input[type="number"]:focus~.label,
.input-container input[type="number"]:valid~.label {
    top: -25px;
    font-size: 16px;
    color: #000;
}

.input-container .underline {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    width: 100%;
    background-color: #000;
    transform: scaleX(0);
    transition: all 0.3s ease;
}

.input-container input[type="number"]:focus~.underline,
.input-container input[type="number"]:valid~.underline {
    transform: scaleX(1);
}

@media (max-width: 1200px) {
    .subscription-form-modal {
        padding: 4rem 15%;
        form {
            .close-modal {
                font-size: 2.2rem;
            }
            label {
                font-size: 1.7rem;
            }
        }
        button {
            min-width: 160px;
            width: auto;
        }
    }
}

@media (max-width: 550px) {
    .subscription-form-modal {
        padding: 2.25rem 5%;
        width: 92.5%;
        .buttons-wrapper {
            justify-content: space-around;
            button {
                width: 45%;
                min-width: 0;
                padding: 0.85rem 1rem;
            }
        }
        form {
            .subsType {
                p {
                    margin: 0.25rem 0.5rem 0.75rem;
                    padding: 0.55rem 1.1rem;
                    &.active {
                        border-radius: 25px;
                    }
                }
            }
        }
    }
}