.categories {
    width: 82.5%;
    margin: 5vh auto;
    .heading {
        border-bottom: 1px solid rgb(170, 170, 170);
        h1 {
            padding-bottom: 1.25rem;
            font-size: 2.3rem;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            .icon {
                margin-right: 1rem;
            }
        }
        .categories-selector-wrapper {
            display: flex;
            .category-selector {
                color: rgb(190, 190, 190);
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 1.1rem;
                font-weight: 700;
                padding: 0.5rem 2.5em 11px;
                width: auto;
                margin-bottom: -2px;
                cursor: pointer;
                &.active {
                    padding-bottom: 8px;
                    color: rgb(30, 30, 30);
                    border-bottom: 3px solid rgb(85, 85, 85);
                }
                .icon {
                    margin-right: 0.5rem;
                }
            }
        }
    }
    .categories-wrapper {
        margin: 1.2rem 0;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
        justify-content: flex-start;
        .category-container {
            //! TO FIX
            width: calc(100%/6.35);
            min-width: 14%;
            width: auto;
            padding: 0.75rem 0.85rem;
            margin: 0 0.4rem;
            box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.65);
            display: flex;
            flex-direction: column;
            align-items: baseline;
            border-radius: 7px;
            transition: .25s;
            // !! !! !! !! !!
            background-color: #161a1d;
            color: #edf2f4;
            &:first-of-type {
                margin-left: 0;
            }
            .category-name {
                font-size: 1.1rem;
                line-height: 1.4rem;
                font-weight: 600;
                display: flex;
                align-items: center;
                justify-content: center;
                letter-spacing: 0.1px;
                .icon {
                    color: #cdcdcd;
                    margin-right: 0.75rem;
                    transition: .25s;
                }
            }
            .cost {
                margin: 1.25rem 0 2.5px;
                font-weight: 600;
                font-size: 1.05rem;
            }
            &:hover {
                background-color: #262c30;
                color: #fff;
                .icon {
                    color: #fff;
                }
            }
        }
    }
}

@media (max-width: 1450px) {
    .categories {
        width: 87.5%;
        .categories-wrapper {
            .category-container {
                min-width: 14.5%;
            }
        }
    }
}

@media (max-width: 1330px) {
    .categories {
        .categories-wrapper {
            .category-container {
                min-width: 15%;
            }
        }
    }
}

@media (max-width: 1200px) {
    .categories {
        width: 92.5%;
        .categories-wrapper {
            .category-container {
                min-width: 19%;
                padding: 0.7rem 0.8rem;
                margin: 0 0.325rem;
                margin-bottom: 1rem;
                &:first-of-type {
                    margin-left: 0.325rem;
                }
                .category-name {
                    font-size: 1rem;
                    width: 100%;
                    font-weight: 500;
                    .icon {
                        margin-right: 0.4rem;
                    }
                }
                .cost {
                    font-size: 1rem;
                    width: 100%;
                    text-align: center;
                    margin-top: 1rem;
                    font-weight: 500;
                }
            }
        }
    }
}

@media (max-width: 800px) {
    .categories {
        .categories-wrapper {
            .category-container {
                min-width: 22.5%;
            }
        }
    }
}

@media (max-width: 550px) {
    .categories {
        width: 95%;
        .heading {
            margin: 0 auto;
            width: 95%;
            h1 {
                font-size: 2.5rem;
                margin-left: 0.25rem;
                justify-content: center;
            }
            .categories-selector-wrapper {
                justify-content: center;
            }
        }
        .categories-wrapper {
            justify-content: center;
            .category-container {
                min-width: 46.5%;
                width: 46.5%;
                padding: 0.75rem 0.8rem;
                margin: 0.5rem 0.35rem;
                &:first-of-type {
                    margin-left: 0.35rem;
                }
                .category-name {
                    font-weight: 600;
                    .icon {
                        margin-right: 0.45rem;
                    }
                }
                .cost {
                    margin-top: 1.15rem;
                    font-weight: 600;
                }
            }
        }
    }
}