@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Maven+Pro:wght@400..900&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Maven Pro", sans-serif;
}

body {
    background-color: #f4f4f4;
}

header {
    nav {
        padding: 1rem 0;
        box-shadow: 0px 1px 10px 0px rgb(170, 170, 170);
        background-color: rgb(0, 0, 255);
        .nav-container {
            width: 75%;
            display: flex;
            justify-content: space-between;
            margin: 0 auto;
            align-items: center;
            img {
                padding: 0.5rem 0;
                width: 62.5px;
                user-select: none;
            }
            button {
                background-color: #fff;
                color: rgb(0, 0, 0);
                border: 2px solid rgb(20, 20, 20);
                font-size: 18px;
                font-weight: 600;
                line-height: 1;
                width: auto;
                height: 4rem;
                max-height: 5rem;
                min-width: 14rem;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 32px;
                padding: 1rem 2rem;
                --tw-shadow: 0px 4px 0px #000000;
                box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
                transform: translate(var(0), var(--tw-translate-y)) rotate(var(0)) skewX(var(0)) skewY(var(0)) scaleX(var(--tw-scale-x)) scaleY(var(--tw-scale-y));
                transition: .15s cubic-bezier(0.4, 0, 0.2, 1);
                cursor: pointer;
                .icon {
                    margin-left: 0.6rem;
                    color: #000;
                }
                &:hover {
                    --tw-shadow: 0px 6px 0px #000000;
                    transform: translateY(-2px) scaleX(1) scaleY(1);
                }
                &:active {
                    --tw-shadow: 0px 2px 0px #000000;
                    transform: translateY(2px) scaleX(1) scaleY(1);
                }
            }
        }
    }
    h2 {
        font-size: 4rem;
        font-weight: 700;
        color: darkblue;
        width: 80%;
        margin: 8vh auto 0vh;
        text-align: center;
        text-transform: uppercase;
        user-select: none;
    }
    .subheading {
        text-align: center;
        font-size: 1.5rem;
        font-weight: 400;
        color: rgb(95, 95, 95);
        margin-top: 0.25rem;
        margin-bottom: 10vh;
        user-select: none;
    }
}

@media (max-width: 550px) {
    header {
        nav {
            .nav-container {
                width: 87.5%;
                img {
                    width: 50px;
                }
                button {
                    padding: 0.75rem 1.25rem;
                    font-size: 17px;
                    font-weight: 700;
                }
            }
        }
        h2 {
            margin: 5.5vh auto 0vh;
            font-size: 2.5rem;
        }
        .subheading {
            font-size: 17px;
            font-weight: 400;
            color: rgb(125, 125, 125);
            margin-top: 0.5rem;
            margin-bottom: 7.5vh;
        }
    }
}